import React, { Component } from "react";
import  '../Stylesheets/slider.css';

import slide1 from "../Images/aalvi1.jpg";
import slide2 from "../Images/aalvi2.png";
import slide3 from "../Images/aalvi4.jpg";

export default class Slider extends Component {
  render() {
    return (
      <div>
       <div id="demo" class="carousel slide" data-ride="carousel" data-interval="2000">
  <ul class="carousel-indicators">
    <li data-target="#demo" data-slide-to="0" class="active"></li>
    <li data-target="#demo" data-slide-to="1"></li>
    <li data-target="#demo" data-slide-to="2"></li>
  </ul>
  <div class="carousel-inner">
    <div class="carousel-item active">
      <img src={slide1} alt="Los Angeles"  className="img-fluid mx-auto d-block " />
      <div class="carousel-caption">
        <h3>Los Angeles</h3>
        <p>We had such a great time in LA!</p>
      </div>   
    </div>
    <div class="carousel-item">
      <img src={slide2} alt="Chicago" className="img-fluid mx-auto d-block " />
      <div class="carousel-caption">
        <h3>Chicago</h3>
        <p>Thank you, Chicago!</p>
      </div>   
    </div>
    <div class="carousel-item">
      <img src={slide3} alt="New York" className="img-fluid mx-auto d-block " />
      <div class="carousel-caption">
        <h3>New York</h3>
        <p>We love the Big Apple!</p>
      </div>   
    </div>
  </div>
  <a class="carousel-control-prev" href="#demo" data-slide="prev">
    <span class="carousel-control-prev-icon"></span>
  </a>
  <a class="carousel-control-next" href="#demo" data-slide="next">
    <span class="carousel-control-next-icon"></span>
  </a>
</div>
      </div>
    );
  }
}
