import React, { Component } from 'react';
import smart from './Images/PizzaLogo.png';
import './Stylesheets/splashcss.css';
 class Splash extends Component {
    render(props) {
      return (
        <div className="col-md-12 col-sm-12 col-lg-12 col-xs-12" style={{backgroundColor: 'green',textAlign: 'center',paddingTop: 16+'em',paddingBottom:17+'em'  }}>
          <img src={smart} alt= 'Smart Ordering Logo' width= '250' height='250' />
          <p style={{color: 'white',fontSize: 24+'px'}}>Smart Order</p>
        </div>
      );
    }
  }
  export default Splash;