import React, { Component } from 'react';
import pic from '../Images/aalvi1.jpg';

export default class teamCard extends Component {
    render() {
        return (
            <div className='row'>

                {
                    this.props.data.map(data =>(
                        <div className="col-md-4 col-lg-4 col-sm-6 col-12" >
            <div className="card" style={{margin: 10+'%',padding: 2+'%',textAlign: "center"}}>
            <img className="card-img-top" src={pic} alt="Card"  style={{width: 100+'%',height: 250+ 'px', borderRadius: 100+'%'}} />
                <div className="card-body" key={data.id}>
              <h4 className="card-title"> {data.name} </h4>
              <p className="card-text"> 
                 <b>{data.Position}</b> <br />
                {data.about} <br />
                
               </p>
              <button  className="btn btn-primary">Contact Now</button> 
              </div>
              </div>
              </div>
                    ))
                }
            </div>
        )
    }
}
