import React, { Component } from 'react';
import pic from '../Images/aalvi1.jpg';

export default class serviceCard extends Component {
    render() {
        return (
            <div className="row">
            
            
            {this.props.dailyData.map(data => (
                <div className="col-md-4 col-lg-4 col-sm-6 col-12" >
            <div className="card" style={{margin: 2+'%'}}>
            <img className="card-img-top" src={pic} alt="Card"  style={{width: 100+'%'}} />
                <div className="card-body" key={data.id}>
              <h4 className="card-title"> {data.title} </h4>
              <p className="card-text"> 
                <b>Size: </b> {data.size} <br />
                <b>Glass: </b>{data.glass} <br />
                <b>Guage: </b>{data.guage} mm <br />
                <b>Price: </b>{data.price} <br />
                <b>Design: </b>{data.design} <br />
               </p>
              <button  className="btn btn-primary">{data.btn_name}</button> 
              </div>
              </div>
              </div>
            ))}
        
        </div>
          
        )
    }
}
