import React, { Component } from 'react';
import ContactForm from './ContactForm';
import Map from './Map';

export default class ContactUs extends Component {
    render() {
        return (

            <div className="container-fluid row">
                <div className="col-md-6 col-lg-6 col-sm-12 col-12 col-xl-6">
                    <Map />
                </div>
                <div className="col-md-6 col-lg-6 col-sm-12 col-12 col-xl-6">
                    <h1 style={{textAlign: "center", margin: 2 + '%'}}>Contact Us !!!</h1>
                    <ContactForm />
                </div>
                
            </div>
        )
    }
}
