import React, { Component } from 'react'
// import Navbar from './Components/Navbar';
import Slider from './Components/Slider';
import Services from './Components/Services';
import TeamMember from './Components/Team';
import ContactUs from './Components/ContactUs';
import Footer from './Components/Footer';

export default class Home extends Component {
    render() {
        return (
            <div>
               <nav className="navbar navbar-expand-sm   navbar-light bg-info"> 
        <a className="navbar-brand" href="#slide">Aalvi Aluminium and Glass</a>   
      <button type="button" className="navbar-toggler" data-toggle="collapse" data-target="#my">
        <span className="navbar-toggler-icon"></span>                     
      </button>
      
   
    <div className="collapse navbar-collapse" id="my">
      <span className='mr-auto' ></span>
      <ul className="navbar-nav navbar-right">
        <li className="nav-item"><a  className="nav-link"href="#slide">Home</a></li>
        <li className="nav-item"><a  className="nav-link"href="#service">Services</a></li>
        <li className="nav-item"><a  className="nav-link"href="#team">About Us</a></li>
        <li className="nav-item"><a  className="nav-link"href="#team">Team</a></li>
        <li className="nav-item"><a  className="nav-link"href="#contact">Contact Us</a></li>
        <li className="nav-item"><a className="nav-link" href="#">Login</a></li>
        <li className="nav-item"><a  className="nav-link"href="#">Sign Up</a></li>
      </ul>
    </div>
    
  
</nav>          
            <div id="slide"><Slider /> </div>
            <div id="service"><Services /></div>
            <div id="team"><TeamMember /></div>
            <div id="contact"><ContactUs /></div>               
               <Footer />
            </div>
        )
    }
}
